.product-details {
  width: 100%;
  max-width: 735px;

  h3 {
    margin-bottom: 1rem;
    text-align: left;
  }

  color: var(--color-gray-800);

  &__text {
    margin-bottom: rem-f(20);
  }

  &__list {
    position: relative;
    margin-bottom: 3rem;

    &::before {
      width: 100%;
      height: 1px;
      background-color: var(--color-gray-300);
      bottom: -24px;
    }
  }
}

.care {
  &__list {
    @include flex;
    flex-direction: column;
    gap: 4px;
  }
  &__item {
    position: relative;
    padding-left: 2rem;

    &::before {
     @include mask;

      left: 0;
      mask-image: url("../img/sprite.svg#hand-wash");
      width: 20px;
      height: 20px;
      background-color: var(--color-gray-700);
    }

    &--hand {
      &::before {
        mask-image: url("../img/sprite.svg#hand-wash");
      }
    }

    &--ironing {
      &::before {
        mask-image: url("../img/sprite.svg#no-ironing");
      }
    }

    &--bleach {
      &::before {
        mask-image: url("../img/sprite.svg#no-bleach");
      }
    }

    &--dry {
      &::before {
        mask-image: url("../img/sprite.svg#no-dry");
      }
    }
  }
}
