.filter {
  width: 100%;
  max-width: 255px;

  &__list {
    @include flex-col;
    gap: rem-f(40);
  }

  &__item {
    .form-search {
      margin-bottom: 1.5rem;
      width: 100%;
      max-width: 100%;
      display: flex;


    }
    &:not(:last-child) {
      position: relative;

      &::before {
        content: "";
        position: absolute;
        bottom: -20px;
        width: 100%;
        height: 1px;
        background-color: var(--color-gray-300);
      }
    }
  }


  &__btn {
    width: 100%;
    max-width: 255px !important;
  }
}

.filter-group {

  &__list {
    display: flex;
    flex-direction: column;

    &--color {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      gap: 8px;
      place-content: center;


      .color-picker__input {
        width: 24px;
        height: 24px;

        &::before {
          width: 2rem;
          height: 2rem;
        }
      }

      .filter-group__item {
        @include flex-col;
        align-items: center;
        gap: 5px;
        margin: 0;
      }
    }
  }

  &__item {
    cursor: pointer;

    &:not(:last-child) {
      margin-bottom: 8px;
    }

    .custom-checkbox__content::before {
      background-color: var(--color-white);
      border: 1px solid var(--color-gray-500);
    }

    .custom-checkbox__field:checked+.custom-checkbox__content::before {
      background-color: var(--color-green);
    }
  }
}
